import Big from "big.js";
import DCTP from './dctp';


/**
 * 使用举例：calc.mulity(a,b,c,d,e)
 * a,b必传
 * 不传c,d,e   计算结果多少就返回多少
 * 不传d,e     默认强制保留c位小数，超出的位数四舍五入
 * 不传e       有多少算多少，最长不超过c位
 *
 *@param a        运算的第一个数
 *@param b        运算的第二个数
 *@param digits   计算结果保留的小数位
 *@param op       计算类型
 *@param type     舍入小数类型 1 -> 四舍五入  2->只舍不入  3 -> 只入不舍
 *@param keepType     保留小数类型 1 -> 有多少算多少  2-> 强制保留小数位  ""->默认有多少算多少
 *
 * */
function operation(a, b, digits, op,type,keepType) {
  let result,n1;
  let flag = false;
  digits = digits || -1;
  type = type || -1;
  keepType = keepType || -1;
  if(DCTP.utils.isEmpty(a) || DCTP.utils.isEmpty(b)){
    return;
  }
  if(DCTP.utils.isNaN(a) || DCTP.utils.isNaN(b)){
    return;
  }
  if((a==0 || !!Number(a)) && (b==0 || !!Number(b))){
    switch (op) {
      case 'add':
        n1 = new Big(a);
        result = n1.plus(b);
        break;
      case 'subtract':
        n1 = new Big(a);
        result = n1.minus(b);
        break;
      case 'multiply':
        n1 = new Big(a);
        result = n1.times(b);
        break;
      case 'divide':
        if(!Number(b)){
          flag = true;
          return;
        }
        n1 = new Big(a);
        result = n1.div(b);
        break;
    }
  } else {
    return;
  }

  if(flag){return;}
  if(digits>=0){
    //result = result.toFixed(digits);
    let realResult = result.toFixed();    //算出的实际值
    let dotPosition = realResult.indexOf(".");   //实际值中小数点的位置

    //type  1 -> 四舍五入  2->只舍不入  3 -> 只入不舍
    //keeptype  1 -> 有多少算多少  2-> 强制保留小数位  ""->默认有多少算多少
    // console.log(dotPosition, realResult.split(".")[1].length,digits,keepType);
    if(dotPosition>0 && realResult.split(".")[1].length>digits){
      //结果不为整数时,且小数点后位数大于保留位数

      if(type === 1){
        result = result.toFixed(digits);
      } else if(type === 2){
        //2->只舍不入,默认保留digits位小数
        //先转字符串，判断小数位要，乘以10的digits次方，保留0位小数（取整），除以10的digits次方，转字符串
        let resultPow = new Big(10);
        resultPow = resultPow.pow(digits);
        let newVal = new Big(realResult);
        newVal = newVal.times(resultPow).toFixed();
        newVal = newVal.substring(0,newVal.indexOf("."));
        newVal = new Big(newVal);
        //console.log(newVal.div(resultPow).toFixed(digits));
        result = newVal.div(resultPow).toFixed();

      } else if(type === 3){
        //3 -> 只入不舍，算多少得多少，最多不超过digits位小数
        //先转字符串，判断小数位，乘以10的digits次方，保留0位小数（取整），加1，转字符串
        let resultPow = new Big(10);
        resultPow = resultPow.pow(digits).toFixed();
        //console.log(resultPow);
        let newVal = new Big(realResult);
        newVal = newVal.times(resultPow);
        //console.log(result);
        //console.log(newVal.toFixed());
        newVal = newVal.plus(1).toFixed();
        newVal = newVal.substring(0,newVal.indexOf("."));
        //console.log(newVal);
        newVal = new Big(newVal);
        result = newVal.div(resultPow).toFixed();
      }else{
        result = result.toFixed(digits);
      }

    } else{
      //结果为整数时
      if( keepType ===2){
        result = result.toFixed(digits)
      } else{
        result = realResult;
      }
    }

  } else {
    result = result.toFixed();
  }
  return result;
};

// 加减乘除的四个接口
let calc = {
  add(a, b, digits,type,keyType) {
    return operation(a, b, digits, 'add',type,keyType);
  },
  subtract(a, b, digits,type,keyType) {
    return operation(a, b, digits, 'subtract',type,keyType);
  },
  multiply(a, b, digits,type,keyType) {
    return operation(a, b, digits, 'multiply',type,keyType);
  },
  divide(a, b, digits,type,keyType) {
    return operation(a, b, digits, 'divide',type,keyType);
  }
};


export default calc;



