<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom: 20px">
            <span>标题：</span>
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入标题"
                      @keyup.native.enter="initData"
                      v-model="queryData.title">
            </el-input>
            <span>持币名称：</span>
            <el-select clearable v-model="queryData.holdCoinId" placeholder="请输入持币名称">
                <el-option
                        v-for="item in virtualList"
                        :key="item.baseSeq"
                        :label="item.chineseName"
                        :value="item.baseSeq">
                </el-option>
            </el-select>
            <el-button class="filter-item" @click="initData" size="small">查询</el-button>
            <el-button type="primary" @click="showAddDialog" v-if="permission.add" size="small">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column type="index" label="序号" width="80"></el-table-column>
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="200">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getCategory(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id,'删除')" v-if="permission.delete">删除
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id,'审核')" v-if="permission.verify">审核
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showDeleteDialog(scope.row.id,'发币')" v-if="permission.sendCoin">发币
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="getDetails(scope.row.id)" v-if="permission.view">查看记录
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>

        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="queryData.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="queryData.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="新增分红计划" :visible.sync="dialogVisible" @close="dialogClose" :close-on-click-modal="false">
            <el-form :model="data" :rules="rules" ref="data">
                <el-form-item :label-width="itemWidth" label="*主题-中文" prop="title">
                    <el-input class="input-short" v-model="data.title"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*主题-英文" prop="titleEn">
                    <el-input class="input-short" v-model="data.titleEn"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="持币名称" prop="holdCoinId">
                    <el-select v-model="data.holdCoinId" placeholder="请选择">
                        <el-option
                                v-for="item in virtualList"
                                :key="item.baseSeq"
                                :label="item.chineseName"
                                :value="item.baseSeq">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="分红币名称" prop="sendCoinId">
                    <el-select v-model="data.sendCoinId" placeholder="请选择">
                        <el-option
                                v-for="item in virtualList"
                                :key="item.baseSeq"
                                :label="item.chineseName"
                                :value="item.baseSeq">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*分红币总数量" prop="amount">
                    <el-input class="input-short" v-model="data.amount"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*分红/持有比例" prop="ratio">
                    <el-input class="input-short" v-model="data.ratio"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*有效开始时间" prop="startTime">
                    <el-date-picker
                            v-model="data.startTime"
                            align="right"
                            :clearable="true"
                            type="datetime"
                            @change="handleChangeStartTime"
                            placeholder="选择时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*有效结束时间" prop="endTime">
                    <el-date-picker
                            v-model="data.endTime"
                            align="right"
                            :clearable="true"
                            type="datetime"
                            @change="handleChangeEndTime"
                            placeholder="选择时间">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="validate">保 存</el-button>
            </div>
        </el-dialog>

        <el-dialog class="p-20" title="查看记录" :visible.sync="dialogTableVisible" :close-on-click-modal="false">
            <span>会员信息：</span>
            <el-input v-model="dialogQueryData.queryStr" class="filter-item" style="width: 300px;margin: 10px"
                      placeholder="请输入会员信息"></el-input>
            <el-button class="filter-item" type="primary" @click.stop="dialogQuery">查询</el-button>

            <el-table :data="dialogTableData">
                <el-table-column type="index" label="序号" width="80"></el-table-column>
                <el-table-column
                        v-for="item in dialogTableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
            </el-table>

            <div class="pagination">
                <el-pagination @size-change="dialogUserPageSize" @current-change="dialogUserPage"
                               :current-page="dialogQueryData.pageNum"
                               :page-sizes="[10,20,30, 50]" :page-size="dialogQueryData.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="dialogTotal">
                </el-pagination>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import fetch from '@/utils/fetch';

    import DCTP from '@/utils/dctp';
    import calc from "@/utils/calculate-big-num";
    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('不能为空'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                dialogTableVisible: false,
                data: {
                    id: '',
                    title: '',
                    titleEn: '',
                    holdCoinId: '',
                    holdName: '',
                    sendCoinId: '',
                    sendName: '',
                    radio: '',
                    amount: '',
                    ratio: '',
                    startTime: '',
                    endTime: '',
                    type: '1',
                    status: '',
                    auditName: '',
                    auditTime: '',
                    undeliveredCount: '',
                    deliveredCount: '',
                },
                action: '',
                rules: {
                    title: [{validator: validate, trigger: 'blur'}],
                    titleEn: [{validator: validate, trigger: 'blur'}],
                    holdCoinId: [{validator: validate, trigger: 'blur'}],
                    sendCoinId: [{validator: validate, trigger: 'blur'}],
                    amount: [{validator: validate, trigger: 'blur'}],
                    ratio: [{validator: validate, trigger: 'blur'}],
                    startTime: [{validator: validate, trigger: 'blur'}],
                    endTime: [{validator: validate, trigger: 'blur'}]
                },
                queryData: {
                    title: '',
                    holdCoinId: '',
                    pageNum: 1,
                    pageSize: 10,
                    type: '1'
                },
                total: 0,
                dialogQueryData: {
                    queryStr: '',
                    pageNum: 1,
                    pageSize: 10,
                    type: '1'
                },
                dialogQueryId: '',
                dialogTotal: 0,
                tableData: [],
                tableHeader: [{
                    label: "标题",
                    prop: "title",
                    width: 120
                }, {
                    label: "状态",
                    prop: "status",
                    width: 120,
                    formatter(row, column, value) {
                        return value === 1 ? '暂存' : (value === 2 ? '已审核' : '');
                    }
                }, {
                    label: "持币名称",
                    prop: "holdName",
                    width: 120
                }, {
                    label: "分红币名称",
                    prop: "sendName",
                    width: 120
                }, {
                    label: "分红币总数量",
                    prop: "amount",
                    width: 120,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "分红比例",
                    prop: "ratio",
                    width: 120,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "待发送",
                    prop: "undeliveredCount",
                    width: 80,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "已发送",
                    prop: "deliveredCount",
                    width: 80,
                    formatter(row, column, value) {
                        return calc.multiply( value , 1 , 12 , 2 , 1 ).toString()
                    }
                }, {
                    label: "有效开始时间",
                    prop: "startTime",
                    width: 180,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value);
                    }
                }, {
                    label: "有效结束时间",
                    prop: "endTime",
                    width: 180,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value);
                    }
                }, {
                    label: "审核人",
                    prop: "auditName",
                    width: 120
                }, {
                    label: "审核时间",
                    prop: "auditTime",
                    width: 180,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value);
                    }
                }],
                dialogTableData: [],
                dialogTableHeader: [{
                    label: "UID",
                    prop: "userId",
                    width: 80
                }, {
                    label: "手机",
                    prop: "userPhone",
                    width: 150
                },
                // {
                //     label: "会员昵称",
                //     prop: "aliasName",
                //     width: 120
                // },
                {
                    label: "邮箱",
                    prop: "email",
                    width: 160
                },
                {
                    label: "真实姓名",
                    prop: "realName",
                    width: 120
                }, {
                    label: "状态",
                    prop: "status",
                    width: 80,
                    formatter(row, column, value) {
                        return value === 1 ? '未发送' : (value === 2 ? '已发送' : '');
                    }
                }, {
                    label: "分得数额",
                    prop: "amount",
                    width: 120
                }, {
                    label: "创建日期",
                    prop: "createTime",
                    width: 180,
                    formatter(row, column, value) {
                        return DCTP.utils.parseDateTimeStr(value);
                    }
                }],
                virtualList: []
            }
        },
        created() {
            this.initData();
        },
        methods: {
            addCategory() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan',
                        method: 'post',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showAddDialog() {
                this.dialogVisible = true;
                this.action = 'add';
            },
            validate() {
                this.$refs['data'].validate((valid) => {
                    if (valid) {
                        switch (this.action) {
                            case 'add':
                                this.addCategory();
                                break;
                            default:
                                this.modify();
                                break;
                        }
                    }
                });
            },
            dialogClose() {
                this.resetData();
            },
            setData(data) {
                let key;
                for (key in this.data) {
                    this.data[key] = data[key] === null ? '' : data[key];
                }
            },
            resetData() {
                let key;
                this.$refs['data'].resetFields();
                for (key in this.data) {
                    switch (key) {
                        case 'type':
                            this.data[key] = '1';
                            break;
                        default:
                            this.data[key] = '';
                    }
                }
                this.action = '';
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            getCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setData(response.data);
                            _this.dialogVisible = true;
                        }
                    }).catch();
                })
            },
            deleteCategory(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/' + id,
                        method: 'delete'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            showDeleteDialog(id, str) {
                this.$confirm('确认' + str + '？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    switch (str) {
                        case '删除':
                            this.deleteCategory(id);
                            break;
                        case '审核':
                            this.verify(id);
                            break;
                        case '发币':
                            this.send(id);
                            break;
                    }
                }).catch(() => {
                    this.$message.info('操作取消');
                });
            },
            modify() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan',
                        method: 'put',
                        data: _this.data
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            query() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/query',
                        method: 'get',
                        params: _this.queryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            initData() {
                if (this.queryData.title === '' && this.queryData.holdCoinId === '') {
                    this.getList();
                    this.getVirtualCoin();
                } else {
                    this.query();
                }
            },
            userPage(val) {
                this.queryData.pageNum = val;
                this.initData();
            },
            userPageSize(val) {
                this.queryData.pageSize = val;
                this.initData();
            },
            dialogUserPage(val) {
                this.dialogQueryData.pageNum = val;
                this.getDetails(this.dialogQueryId);
            },
            dialogUserPageSize(val) {
                this.dialogQueryData.pageSize = val;
                this.getDetails(this.dialogQueryId);
            },
            getVirtualCoin() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/virtualcoinbases',
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.virtualList = response.data.data;
                        }
                    }).catch();
                });
            },
            handleChangeStartTime(time) {
                this.data.startTime = time;
            },
            handleChangeEndTime(time) {
                this.data.endTime = time;
            },
            verify(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/check/' + id,
                        method: 'put'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            send(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/send/' + id,
                        method: 'put'
                    }).then(function (response) {
                        if (response.result) {
                            _this.initData();
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            getDetails(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/detail/' + id,
                        method: 'get',
                        params: _this.dialogQueryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogTableVisible = true;
                            _this.dialogTableData = response.data.data;

                            console.log( response.data.data )

                            _this.dialogQueryId = id;
                            _this.dialogTotal = response.data.page.count;
                        } else {
                            _this.getMessage(response.msg, response.result);
                        }
                    }).catch();
                })
            },
            dialogQuery(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/shareplan/detail/' + _this.dialogQueryId + '/query',
                        method: 'get',
                        params: _this.dialogQueryData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogTableData = response.data.data;

                            console.log( response.data.data )

                            _this.dialogTotal = response.data.page.count;
                        }
                    }).catch();
                })
            }
        }
    }
</script>



